.faq{
    padding-top: 180px;
    padding-bottom: 190px;
}
.faq-title{
    font-style: normal;
font-weight: bold;
font-size: 36px;
line-height: 48px;
color: #D4C17F;
margin: 0;
}
.faq-form__p{
    width: 255px;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    color: #E7E7E7;
}
.faq-form__item{
    width: 200px;
height: 45px;
font-style: normal;
background-color: rgba(117, 123, 128, 0.438);
border: none;
font-weight: normal;
font-size: 14px;
line-height: 27px;
text-align: center;
margin: 15px;
color: #E7E7E7;
}
.faq-form__item::placeholder{
    color: #e7e7e7;
}
.faq-form__item-mod{
    background: #D4C17F;
    opacity: 1;
    color: white;
    text-align: center;
    border: none;
}
.faq-form__item-mod:hover{
    background: rgb(163, 148, 93);
}
.faq-form{
    margin-top: 60px;
    display: flex;
    justify-content: space-around;
}
@media screen and (max-width: 377px){
    .faq-form{
        flex-wrap: wrap;
        margin-top:10px;
    }
    .faq-title{
        font-size: 30px;
        text-align: center;
        padding-bottom: 20px;
    }
    .faq{
        padding-top: 80px;
        padding-bottom: 50px;
    }
}
@media screen and (min-width: 377px) and (max-width: 576px){
    .faq-form{
        flex-wrap: wrap;
        margin-top:10px;
    }
    .faq-title{
        font-size: 30px;
        text-align: center;
        padding-bottom: 20px;
    }
    .faq{
        padding-top: 80px;
        padding-bottom: 50px;
    }
    .faq-form__item{
        width: 300px;
    }
}
@media screen and (min-width: 577px) and (max-width: 767px){
    .faq-form{
        flex-wrap: wrap;
        margin-top:10px;
    }
    .faq-title{
        font-size: 30px;
        text-align: center;
        padding-bottom: 20px;
    }
    .faq{
        padding-top: 80px;
        padding-bottom: 50px;
    }
    .faq-form__item{
        width: 500px;
    }
}